var func = {
    example : function(){
        console.log('sample common');
    },
    getget : function(){
        var url = document.URL;
        // console.log(url);
        url = url.split("?");
        var result = new Object();
        if(url[1]) {
            var get = url[1];
            let inx;
            get = get.split("&");
            for(inx in get) {
                var str = get[inx].split("=");
                if(str.length == 2) {
                    result[str[0]] = str[1];
                } else {
                    var value = "";
                    for(var i = 1; i < str.length; i++) {
                        if(i == 1) {
                            value += str[i];
                        } else {
                            value += "=" + str[i];
                        }
                    }
                    result[str[0]] = value;
                }
            }
        }
        return result;
    },
    // 取得時間格式
    getTimeFormat: function(format, tim='') {
        // 給input  date設定預設值
        if(tim) {
            var now = new Date(tim);
        } else {
            var now = new Date();
        }
        //格式化日，如果小於9，前面補0
        var day = ("0" + now.getDate()).slice(-2);
        //格式化月，如果小於9，前面補0
        var month = ("0" + (now.getMonth() + 1)).slice(-2);
        //格式化時，如果小於9，前面補0
        var hour = ("0" + (now.getHours())).slice(-2);
        //格式化分，如果小於9，前面補0
        var min = ("0" + (now.getMinutes())).slice(-2);
        //格式化秒，如果小於9，前面補0
        var sec = ("0" + (now.getSeconds())).slice(-2);
        // 格式轉換
        format = format.replace('Y', now.getFullYear());
        format = format.replace('m', month);
        format = format.replace('d', day);
        format = format.replace('H', hour);
        format = format.replace('i', min);
        format = format.replace('s', sec);
        return format;
        //完成賦值
        // document.getElementById(id).value = today;
    },
    // 取得radio的值
    getRadioValue: function(name){
        let radios = document.getElementsByName(name);
        if(radios.length == 0) { 
            return false;   
        }
        let inx;
        let val = null;
        for(inx in radios) {
            if(radios[inx].checked){
                val = radios[inx].value;
                break;
            }
        }
        return val;
    },
    getBytelength : function (str = ' ') {
        var arr = str.match(/[^\x00-\xff]/ig);
        return (arr == null) ? str.length : str.length + arr.length;
    },
    padleft(str, len, text) {
        str = str.toString();
        let nl = this.getBytelength(str);
        let num = len - nl;
        let i = 0;
        for(i = 0; i < num; i++ ) {
            str = text + str;
        }
        return str;
    },
    padright(str, len, text) {
        str = str.toString();
        let nl = this.getBytelength(str);
        let num = len - nl;
        let i = 0;
        for(i = 0; i < num; i++ ) {
            str += text;
        }
        return str;
    },
    getMod26Cod(string, cod) {
        if(cod == 'BPF') {
            cod = 'BPE';
        }
        string = string.toString() + cod;
        let abcstr = 'ZABCDEFGHIJKLMNOPQRSTUVWXY';
        let arr = abcstr.split('');
        let abcArr = {};
        let inx = '';
        for(inx in arr) {
            abcArr[parseInt(inx)] = arr[inx];
        }
        abcstr = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
        arr = abcstr.split('');
        let cbaArr = {};
        for(inx in arr) {
            cbaArr[arr[inx]] = parseInt(inx)+1;
        }
        let strArr = string.split('');
        let num = 0;
        for(inx in strArr) {
            if(!isNaN(strArr[inx])) {
                num = num + parseInt(strArr[inx]);
            } else {
                num = num + parseInt(cbaArr[strArr[inx]]);
            }
        }
        return string + abcArr[num%26] + '!';
    },
    checkModCod(string, cod) {
        string = string.toString();
        let ex = string.split(cod);
        if(!ex[1]) {
            return false;
        } else {
            let newcod = this.getMod26Cod(ex[0], cod);
            if(string.toUpperCase() == newcod.toUpperCase()) {
                return ex[0];
            } else {
                return false;
            }
        }
    },
    sortTable(event){
        console.log(event.target);
        let th = event.target;
        let tempSort = document.getElementById("tmp_json").value;
        if(!tempSort) {
            tempSort = new Object();
        } else {
            tempSort = JSON.parse(tempSort);
        }
        if(!tempSort[th.sourceIndex + "th"]) {
            tempSort[th.sourceIndex + "th"] = 1;
        } else {
            tempSort[th.sourceIndex + "th"] = tempSort[th.sourceIndex + "th"] * (-1);
        }
        console.log(tempSort[th.sourceIndex + "th"]);
        let table = th.offsetParent;
        let ft = table.rows[0].cells;
        let target = 0;
        let inx = 0;
        for(inx in ft) {
            if(ft[inx].innerHTML == th.innerHTML) {
                target = inx;
                break;
            }
        }
        let compar = new Object();
        let oldRow = new Object();
        for(inx in table.rows) {
            oldRow[inx] = table.rows[inx].innerHTML;
            if(inx > 0) {
                compar[inx] = table.rows[inx].cells[target].innerText;
            }
        }
        
        let temp = [];
        for (inx in compar) {
            temp.push([inx, compar[inx]]);
        }
        temp.sort(function(a, b) {
            //如果是數字 做數字排序
            if(!isNaN(a[1]) && !isNaN(b[1])) {
                return (a[1] - b[1]) * tempSort[th.sourceIndex + "th"];
            } else {
                //如果是字串做字串排序
                return (a[1].localeCompare(b[1])) * tempSort[th.sourceIndex + "th"];
            }
        });
        let sortObj = new Object();
        for(inx in temp) {
            sortObj["_"+temp[inx][0]] = temp[inx][1];
        }
        let count = 1;
        for(inx in sortObj) {
            //inx 舊位置
            var oldSide = inx.split("_")[1];
            //count 新位置
            table.rows[count].innerHTML = oldRow[oldSide];
            count++;
        }
        document.getElementById("tmp_json").value = JSON.stringify(tempSort);
    },
    sleep(milliseconds) {
        var start = new Date().getTime();
        for (var i = 0; i < 1e7; i++) {
            if ((new Date().getTime() - start) > milliseconds){
            break;
            }
        }
    }
}
export default func = func;


