import {Base64} from 'js-base64'
import axios from 'axios'

axios.defaults.timeout = 60000

var gows = {
    callWS : async function (tag, param){
        let wsParam = new FormData();
        wsParam.append('var', Base64.encode(JSON.stringify(param)));
        return new Promise((resolve, reject) => {
            axios.post('http://eip.sltung.com.tw/EIP/gows.php?wtag=' + tag, wsParam).then(result => {
            // axios.post('https://neip.sltung.com.tw/EIP/gows.php?wtag=' + tag, wsParam).then(result => {
            // axios.post('https://dmzeip443.sltung.com.tw/EIP/gows.php?wtag=' + tag, wsParam).then(result => {
            // axios.post('http://10.6.51.133/EIP/gows.php?wtag=' + tag, wsParam).then(result => {
                let rt = JSON.parse(Base64.decode(result.data));
                try {
                    resolve(rt);
                } catch (e) {
                    console.log(e)
                }
            }),err => {
                reject(err);
            }
        });
    }
}
export default gows = gows;


