var getget = function(){
    var url = document.URL;
    url = url.split("?");
    var result = new Object();
    if(url[1]) {
        var get = url[1];
        let inx;
        get = get.split("&");
        for(inx in get) {
            var str = get[inx].split("=");
            if(str.length == 2) {
                result[str[0]] = str[1];
            } else {
                var value = "";
                for(var i = 1; i < str.length; i++) {
                    if(i == 1) {
                        value += str[i];
                    } else {
                        value += "=" + str[i];
                    }
                }
                result[str[0]] = value;
            }
        }
    }
    return result;
}
var setUser = async function(sid = ''){
    // 取得頁面參數
    var getParam = getget();
    // console.log(getParam.sid);
    var now = Date.now();
    var diffTime = now - localStorage.loginTime;
    let loginSid = (sid) ? sid : getParam.sid;
    if(!loginSid) {
        // 檢查是否曾經有登入過
        if(localStorage.sid) {
            if(diffTime < 86400 * 1000) {
                // 登入沒超過一天開放通行
                this.$store.state.emp_no = localStorage.emp_no;
                this.$store.state.emp_nam = localStorage.emp_nam;
            } else {
                // localStorage清乾淨
                localStorage.clear();
            }
        } else{
            localStorage.clear();
        }
    } else {
        // SID 認證機制
        console.log(1);
        let param = {
            'wb_base64' : "0",
            'wb_big5' : "0",
            'sid' : loginSid
        };
        console.log(2);
        let rt = await this.$gows.callWS('evt.sso.chksid', param);
        console.log(3);
        if(rt.sts == '000000') {
            let userData;
            userData = JSON.parse(rt.val);
            localStorage.setItem('sid', loginSid);
            localStorage.setItem('emp_no', userData.emp_no);
            localStorage.setItem('emp_nam', userData.emp_nam);
            localStorage.setItem('depart', userData.depart);
            localStorage.setItem('nam_a', userData.nam_a);
            localStorage.setItem('loginTime', Date.now());
            this.$store.state.emp_no = userData.emp_no;
            this.$store.state.emp_nam = userData.emp_nam;
            this.$store.state.depart = userData.depart;
            // 先寫
            // this.$store.state.depart = 1180;
        } else {
            localStorage.clear();
            alert('SID失效，無法登入');
        }
    }
    return new Promise((resolve) => {
        resolve(true);        
    });
}
export default setUser = setUser;


