<style scoped>
    .header {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        height: 70px;
        z-index: 99;
    }
    .function_div {
        margin-top: 70px;
        background-color:#FFFFFF; 
        padding: 15px;
    }
    .content {
        background-color:#FFFFFF; 
        height:70%; 
        padding: 15px; 
        /* overflow-y: scroll; */
    }
    .function_list {
        line-height: 30px;
    }
    .scrollbar::-webkit-scrollbar {
        width: 7px;
    }
    .scrollbar::-webkit-scrollbar-button {
        background: transparent;
    }
    .scrollbar::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    }
    .scrollbar::-webkit-scrollbar-thumb {
        background-color: #949494;
        border-radius: 5px;
    }
    .sort_item {
        cursor: pointer;
        padding: 5px 10px;
        border-radius: 20px;
        transition: background-color 0.3s ease;
    }
    
    .sort_item.active {
        background-color: #80808061;
    }
    .sort_direction {
        margin-left: 5px;
        font-size: 10px;
    }  
    .sort_quantity {
        display: inline-block;
        cursor: pointer;
        margin-left: 10px;
        padding: 5px;
        border-radius: 10px;
        transition: background-color 0.3s ease;
        line-height: 20px;
    }
    .sort_quantity.active {
        background-color: #80808061;
    }
    .clothesImg{
        max-width: 100%;
    }
    .pagination {
        display: flex;
        justify-content: center;
        margin-top: 20px;
    }
    
    .pagination .page-link {
        display: inline-block;
        padding: 5px 10px;
        margin: 0 5px;
        border: 1px solid #ccc;
        background-color: #fff;
        color: #333;
        text-decoration: none;
        cursor: pointer;
    }
    
    .pagination .page-link.active {
        background-color: #333;
        color: #fff;
    }
    .qr-code-icon {
        position: fixed;
        bottom: 20px;
        right: 20px;
        z-index: 9999;
        background-color: #ffffff;
        width: 60px;
        height: 60px;
        border: 2px solid #888888;
        border-radius: 50%;
        display: none;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
    }
    .qr-code-icon:hover {
        background-color: #f3f3f3;
    }
    @media only screen and (max-width: 767px) {
        .qr-code-icon {
            display: flex;
        }
    }
</style>>
<template>
    <div>
        <div class="header">
            <headerDiv @setChooseType="setChooseType"></headerDiv>
        </div>
        <div class="function_div">
            <h2><b>{{typeTitle}}</b></h2>
            <div class="function_list">
                排序：
                <span class="sort_item" v-for="item in sortItems" :key="item.id" :class="{ active: item.isActive }" @click="toggleSortItem(item)">
                    {{ item.label }} 
                    <span class="sort_direction" v-show="item.isActive">{{ item.sortDirection }}</span>
                </span>
                <br>
                數量：
                <span class="sort_quantity" v-for="quantityItem in quantityItems" :key="quantityItem.value" :class="{ active: quantityItem.isActive }" @click="toggleQuantityItem(quantityItem)">
                    {{ quantityItem.value }}
                </span>
            </div>
        </div>    
        <div class="content">
            <!--{{clothesData }}-->
            <div class="container text-center">
                <div class="row">
                    <div id="carouselExampleIndicators" class="carousel slide banner" data-bs-ride="carousel">
                        <ol class="carousel-indicators">
                            <li data-bs-target="#carouselExampleIndicators" data-bs-slide-to="0" class="active"></li>
                            <li data-bs-target="#carouselExampleIndicators" data-bs-slide-to="1"></li>
                            <!-- 在此添加更多的 carousel indicators -->
                        </ol>
                        <div class="carousel-inner">
                            <div class="carousel-item active">
                                <img src="https://shoplineimg.com/53fc3e76f577fe4751000010/625acdb33fbc630030981550/2160x.webp?source_format=png" class="d-block w-100" alt="Banner 1">
                            </div>
                            <div class="carousel-item">
                                <img src="https://shoplineimg.com/53fc3e76f577fe4751000010/625acdb33fbc630030981550/2160x.webp?source_format=png" class="d-block w-100" alt="Banner 2">
                            </div>
                            <!-- 在此添加更多的 carousel items -->
                        </div>
                        <a class="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-bs-slide="prev">
                            <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                            <span class="visually-hidden">Previous</span>
                        </a>
                        <a class="carousel-control-next" href="#carouselExampleIndicators" role="button" data-bs-slide="next">
                            <span class="carousel-control-next-icon" aria-hidden="true"></span>
                            <span class="visually-hidden">Next</span>
                        </a>
                    </div>
                </div>
                <div class="row">
                    <div class="col-6 col-sm-6 col-lg-3" v-for="(item, index) in displayedClothes" :key="index" align="left">
                        <img :src="item.image" alt="衣服圖片" class="clothesImg">
                        <div>{{index + 1}}-{{ item.name }}</div>
                        <div>價格: {{ item.price }}</div>
                        <div>上架日期:<br>{{ item.date }}</div>
                        <div>庫存: {{ item.stock }}</div>
                    </div>
                </div>
                <div class="pagination">
                    <button class="page-link" v-for="page in totalPages" :key="page" :class="{ active: page === currentPage }" @click="changePage(page)">{{ page }}</button>
                </div>
            </div>
        </div>
        <div class="qr-code-icon">
            <svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" fill="currentColor" class="bi bi-qr-code-scan" viewBox="0 0 16 16">
                <path d="M0 .5A.5.5 0 0 1 .5 0h3a.5.5 0 0 1 0 1H1v2.5a.5.5 0 0 1-1 0v-3Zm12 0a.5.5 0 0 1 .5-.5h3a.5.5 0 0 1 .5.5v3a.5.5 0 0 1-1 0V1h-2.5a.5.5 0 0 1-.5-.5ZM.5 12a.5.5 0 0 1 .5.5V15h2.5a.5.5 0 0 1 0 1h-3a.5.5 0 0 1-.5-.5v-3a.5.5 0 0 1 .5-.5Zm15 0a.5.5 0 0 1 .5.5v3a.5.5 0 0 1-.5.5h-3a.5.5 0 0 1 0-1H15v-2.5a.5.5 0 0 1 .5-.5ZM4 4h1v1H4V4Z"/>
                <path d="M7 2H2v5h5V2ZM3 3h3v3H3V3Zm2 8H4v1h1v-1Z"/>
                <path d="M7 9H2v5h5V9Zm-4 1h3v3H3v-3Zm8-6h1v1h-1V4Z"/>
                <path d="M9 2h5v5H9V2Zm1 1v3h3V3h-3ZM8 8v2h1v1H8v1h2v-2h1v2h1v-1h2v-1h-3V8H8Zm2 2H9V9h1v1Zm4 2h-1v1h-2v1h3v-2Zm-4 2v-1H8v1h2Z"/>
                <path d="M12 9h2V8h-2v1Z"/>
            </svg>
        </div>
        <div class="modal" tabindex="-1" id="this.alertModal">
            <div class="modal-dialog">
                <div class="modal-content">
                <div class="modal-header" style="background-color: cornflowerblue;">
                    <h5 class="modal-title" id="alertTitle"></h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div class="modal-body">
                    <p id="alertMsg"></p>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">關閉</button>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import Header from './header.vue'
axios.defaults.timeout = 6000

// components
import header from '@/components/header_new'

export default {
    name: 'index',
    components: {
        "headerDiv" : header,
       
    },
    data () {
        return {
            "typeTitle" : "",
            "sortItems": [
                { id: 1, label: '價格', isActive: false, sortDirection: '∨' },
                { id: 2, label: '庫存', isActive: false, sortDirection: '∨' },
                { id: 3, label: '上架時間', isActive: false, sortDirection: '∨' }
            ],
            "quantityItems": [
                { id: 1, value: '24', isActive: true },
                { id: 2, value: '48', isActive: false },
                { id: 3, value: '72', isActive: false }
            ],
            "clothesData": [],
            displayedClothes:[],
            itemsPerPage: 24, // 每页显示的数量
            currentPage: 1 // 当前页码
        }
    },
    watch : {
        
    },
    computed: {
        totalPages() {
            return Math.ceil(this.clothesData.length / this.itemsPerPage);
        }
    },
    methods: {
        async setChooseType(item){
            console.log(item);
            if(item.choose == -1) {
                this.typeTitle = item.label;
            } else {
                this.typeTitle = item.label + '-' + item.dropdown[item.choose].label;
            }
        },
        alertModal(title='系統訊息', msg = '') {
            var myModal = new bootstrap.Modal(document.getElementById('this.alertModal'), {
                keyboard: false
            });
            document.getElementById('alertTitle').innerHTML = title;
            document.getElementById('alertMsg').innerHTML = msg;
            myModal.show();
        },
        toggleSortItem(item) {
            this.sortItems.forEach((sortItem) => {
                if (sortItem === item) {
                    sortItem.isActive = true;
                    sortItem.sortDirection = sortItem.sortDirection === '∨' ? '∧' : '∨';
                } else {
                    sortItem.isActive = false;
                }
            });

            // 根据当前排序项对 clothesData 进行排序
            if (item.isActive) {
                if (item.label === '價格') {
                    this.clothesData.sort((a, b) => {
                        return item.sortDirection === '∨' ? a.price - b.price : b.price - a.price;
                    });
                } else if (item.label === '庫存') {
                    this.clothesData.sort((a, b) => {
                        return item.sortDirection === '∨' ? a.stock - b.stock : b.stock - a.stock;
                    });
                } else if (item.label === '上架時間') {
                    this.clothesData.sort((a, b) => {
                        const dateA = new Date(a.date);
                        const dateB = new Date(b.date);
                        return item.sortDirection === '∨' ? dateA - dateB : dateB - dateA;
                    });
                }
            }
            this.currentPage = 1;
            this.updateDisplayedClothes();
        },
        toggleQuantityItem(item) {
            // console.log(item);
            this.itemsPerPage = parseInt(item.value);
            this.quantityItems.forEach((quantityItem) => {
                if (quantityItem === item) {
                    quantityItem.isActive = !quantityItem.isActive;
                } else {
                    quantityItem.isActive = false;
                }
            });
            this.updateDisplayedClothes();
        },
        scrollToTop() {
            window.scrollTo({
                top: 0,
                behavior: 'smooth' // 使用平滑滚动效果
            });
        },
        changePage(page) {
            this.currentPage = page;
            this.updateDisplayedClothes();
            this.scrollToTop();
        },
        updateDisplayedClothes() {
            const startIndex = (this.currentPage - 1) * this.itemsPerPage;
            const endIndex = startIndex + this.itemsPerPage;
            console.log(startIndex, endIndex);
            this.displayedClothes = this.clothesData.slice(startIndex, endIndex);
        },
        generateData(count) {
            const prices = [580, 690, 790];
            for (let i = 0; i < count; i++) {
                const randomNum = Math.floor(Math.random() * 100000);
                const today = new Date();
                const oneMonthAgo = new Date(today.getFullYear(), today.getMonth() - 1, today.getDate());
                const randomDate = new Date(oneMonthAgo.getTime() + Math.random() * (today.getTime() - oneMonthAgo.getTime()));
                const randomStock = Math.floor(Math.random() * 46) + 5;
                const randomPrice = prices[Math.floor(Math.random() * prices.length)];
                const item = {
                    image: 'https://shoplineimg.com/53fc3e76f577fe4751000010/635f7b8c375ff90001c60529/400x.webp?source_format=jpg',
                    name: `床編故事聯名T | 1042${randomNum.toString().padStart(5, '0')}`,
                    price: randomPrice,
                    date: randomDate.toISOString().split('T')[0],
                    stock: randomStock
                };
                this.clothesData.push(item);
            }
            this.updateDisplayedClothes();
        }
    },
    async mounted() {
        let defaultLable = {
            "label": "暢銷排行榜",
            "dropdown": [
                {
                    "label": "月榜"
                },
                {
                    "label": "周榜"
                }
            ],
            "choose": 0
        };
        this.setChooseType(defaultLable);
        this.generateData(45);
        console.log(this.clothesData);
    }
}
</script>



